import { mapGetters } from 'vuex';
import stepTwo from '../myInfo/step2.vue';
export default {
	data() {
		return {
			res:'',
			loading:true,
		};
	},
	components: {
		stepTwo
	},
	onLoad() {
		this.init()
	},
	computed:{
		...mapGetters(['id']),
	},
	methods:{
		init(){
			uni.showLoading({
			    title: '加载中'
			});
			this.$http.get('personwx.personinfo/1.0/',{
				id:this.id
			}).then(res =>{
				console.log(res)
				this.res = this.$utils._get(res,'data.data','{}')
				this.loading = false
				uni.hideLoading();
			})
		},
	}
}
